#header {
    position: fixed;
    display: flex;
    width: 100%;
    height: 6.375rem;
    background-color: #F2E9D8;
    align-items: center;
    z-index: 999;
}

.logo {
    width: 8.125rem;
    height: 2.5rem;
    background-image: url(../img/logo.svg);
    background-size: 100% 100%;
    margin-left: 20rem;
    cursor: pointer;
}

.hamburger {
    display: none;
}

.menu {
    position: absolute;
    display: flex;
    align-items: center;
    right: 20rem;
}

.wrap {
    display: flex;
    width: 13.375rem;
    height: 1.25rem;
    justify-content: space-between;
}

.home,
.about,
.support {
    font-size: 1rem;
    font-weight: 400;
    cursor: pointer;
}

.download {
    width: 8.75rem;
    height: 3.125rem;
    border: none;
    border-radius: 40px;
    background-color: #D97652;
    font-size: 1rem;
    font-weight: 700;
    color: #fff;
    margin-left: 2.5rem;
    cursor: pointer;
}

/**Tablet 768X481**/
@media all and (max-width: 769px){
    .logo {
        margin-left: 12rem;
    }
    
    .menu {
        right: 12rem;
    }

    .wrap {
        width: 18rem;
    }
}

/* moblie */

/**Mobile L 425X481**/
@media all and (max-width: 426px){
    #header {
        width: 100vw;
        height: 10.133rem;
        background-color: #F2E9D8;
    }
    
    .logo {
        width: 12.267rem;
        height: 3.733rem;
        margin-left: 2.667rem;
    }

    .hamburger {
        position: absolute;
        display: block;
        width: 4.267rem;
        height: 4.267rem;
        background-image: url(../img/menu.svg);
        background-size: 100% 100%;
        right: 2.667rem;
        cursor: pointer;
    }
    
    .menu {
        display: none;
    }
}
