#first {
    height: 56.25rem;
    background-color: #F2E9D8;
    overflow: hidden;
}

.wrap {
    position: relative;
    display: flex;
    width: 100%;
    height: 100%;
    align-items: center;
}

.textWrap {
    position: absolute;
    display: flex;
    flex-direction: column;
    height: 21rem;
    align-items: flex-start;
    justify-content: space-between;
    left: 20rem;
}

.textWeb {
    display: block;
}

.text {
    font-size: 3rem;
    font-weight: 700;
    line-height: 3.756rem;
}

.detailText {
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.252rem;
}

.mobileText {
    display: none;
}

.btn {
    width: 8.75rem;
    height: 3.125rem;
    border: none;
    border-radius: 40px;
    background-color: #D97652;
    font-size: 1rem;
    font-weight: 700;
    color: #fff;
    cursor: pointer;
}

.img {
    position: absolute;
    width: 25.938rem;
    height: 55.625rem;
    background-image: url(../img/box-cat.png);
    background-size: 100% 100%;
    right: 20rem;
    margin-top: 13.375rem;
}

/**Tablet 768X481**/
@media all and (max-width: 769px){
    .textWrap {
        left: 12rem;
    }
    
    .detailText {
        line-height: 1.5rem;
    }

    .img {
        right: 12rem;
    }
}

/* moblie */

/**Mobile L 425X481**/
@media all and (max-width: 426px){
    #first {
        width: 100vw;
        height: 96.8rem;
        padding-top: 10.133rem;
    }
    
    .textWrap {
        display: flex;
        flex-direction: column;
        width: 37.333rem;
        height: 38.4rem;
        text-align: center;
        align-items: center;
        top: 5.333rem;
        margin-left: -4rem;
    }
    
    .text {
        font-size: 3.2rem;
        line-height: 4.007rem;
    }

    .textMobile {
        display: block;
    }

    .webText {
        display: none;
    }

    .mobileText {
        display: block;
    }
    
    .detailText {
        font-size: 1.867rem;
        line-height: 2.337rem;
    }

    .btn {
        width: 15.867rem;
        height: 5.333rem;
        font-size: 1.867rem;
        line-height: 2.337rem;
    }

    .img {
        /* position: absolute; */
        width: 26.267rem;
        height: 56.267rem;
        background-image: url(../img/box-cat.png);
        background-size: 100% 100%;
        bottom: -8rem;
    }
}